<template>
    <div class="dialog-container" @click="closeDialog">
        <div class="dialog-box" :class="{'payment-dialog-box-pc': isPc, 'payment-dialog-box-mobile': !isPc}">
            <div class="card-wrap">
                <!--<div class="common-box loading-box" v-if="JSON.stringify(invoiceInfo) === '{}'">
                    <a-spin>
                        <a-icon slot="indicator" type="loading" style="font-size: 80px; color: #3963EF; margin: 80px 0px;" spin/>
                    </a-spin>
                </div>-->
                <div class="common-box no-account-success-box" v-if="!isLogin && paySuccess">
                    <i class="success-icon"></i>
                    <span class="success-title">{{$t('PaymentPage.Pay-Success')}}</span>
                    <span class="success-description">{{$t('PaymentPage.Check-Email')}}</span>
                </div>
                <div class="common-box account-success-box" v-if="isLogin && paySuccess">
                    <span class="success-title">{{$t('PaymentPage.Pay-Service2')}}</span>
                    <span class="success-description">{{$t('PaymentPage.Service-Ready')}}</span>
                    <div class="account-info-box">
                        <AccountAddress v-bind:userAccount="userAccount"></AccountAddress>
                        <div class="btn-box">
                            <div class="steps-lines">
                                <a-progress :percent="percent" :show-info="false" size="small"/>
                            </div>
                            <a-button class="start-btn" :class="{'btn-disabled': loadSec != 3}" type="primary" @click.stop="startNow()" :disabled="loadSec != 3">
                                {{$t('PaymentPage.Start-Use')}}
                            </a-button>
                        </div>
                    </div>
                </div>
                <div class="common-box paying-box" v-if="!paySuccess && JSON.stringify(invoiceInfo) !== '{}'">
                    <div class="paying-invoice-detail-box">
                        <div class="first-row-box" v-if="!paySuccess">
                            <span class="price-span">{{invoiceInfo.balance}}<span class="unit-span">USD</span></span>
                            <span>{{$t('PaymentPage.Bill-No')}}: {{invoiceInfo.invoiceid}}</span>
                        </div>
                        <span v-if="!paySuccess">{{$t('PaymentPage.Pay-Date')}}: {{invoiceInfo.duedate}}</span>
                        <!--<span class="check-invoice-detail" v-if="false" @click="enterInvoiceDetail()">{{$t('PaymentPage.Bill-Detail')}}</span>-->
                    </div>
                    <div class="payment-box">
                        <div class="switch-pay-method" v-if="!paySuccess">
                            <div class="left-pay-method">
                                <span>支付寶支付</span>
                                <i class="alipay"></i>
                            </div>
                            <span class="switch-span" v-if="false">其他</span>
                        </div>
                        <div class="pay-status-box" v-if="!paySuccess">
                            <div class="pay-sync" id="pay-sync" v-if="isPaying">
                                <a-spin v-if="!paySuccess && !payFailed">
                                    <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #3963EF;" spin/>
                                </a-spin>
                                <a-icon v-if="paySuccess && !payFailed" type="check-circle" theme="twoTone" twoToneColor="#44B496"/>
                                <a-icon v-if="payFailed" type="close-circle" theme="twoTone" twoToneColor="#F2585E"/>
                                <span class="sync-result">{{payStatus}}</span>
                            </div>
                            <span v-if="(!payFailed && isPaying) || paySuccess" class="pay-tips-center">{{footerText}}</span>
                            <span v-if="payFailed && isPaying" class="pay-tips-center">{{$t('PaymentPage.Pay-Fail')}}<a @click.stop="openZendesk()">{{$t('PaymentPage.Contact-Customer-Service')}}</a></span>
                            <div class="btn-container" id="btn-container">
                                <a-button class="submit-btn" type="primary" id="submit-btn"
                                          @click.stop="payNow()" :disabled="payUrl === ''">
                                    {{!isPaying ? invoiceInfo.balance + ' USD' : ''}} {{btnTips}}
                                </a-button>
                                <a class="submit-btn" style="position: absolute;background-color: transparent !important;" :href="payUrl" target="_blank" v-if="!isPaying && payUrl !== ''" @click.stop="payNow()"></a>
                                <a-button class="submit-btn2" type="primary" id="submit-btn2" ghost
                                          @click.stop="userPaid()">
                                    {{$t('PaymentPage.Already-Pay')}}
                                </a-button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {TweenMax} from 'gsap';
    import i18n from '../i18n';
    import AccountAddress from "../components/AccountAddress";
    import {AU_AFF, AU_HMAC, AU_UUID, AU_IN_APP} from "../requestServer";
    import { UPDATE_INFO } from "../eventBus";

    export default {
        name: 'PaymentDialog',
        components: {
            AccountAddress
        },
        data() {
            return {
                userAccount: {},
                loading: false,
                resendTip: false,
                secondNum: 60,
                invoiceInfo: {},
                isAndroid: false,
                isIos: false,
                isPc: this.$isPc(),
                isLogged: false,
                isLogin: false,
                isRenew: false,
                btnTips: this.$t('order/pay.Status-1'),
                payStatus: this.$t('order/pay.Paragraph-1'),
                paySuccess: false,
                payFailed: false,
                invoiceId: 0,
                intervalId: '',
                footerText: this.$t('order/pay.Paragraph-2'),
                payMethodList: [
                    {
                        id: 0,
                        name: 'order/place:screen4.Tab1',
                        logo: [{name: 'alipay', url: `pay/alipay.svg`}],
                        type: 'pkjeub-alipay'
                    }
                ],
                isSentData: false,
                payUrl: '',
                isPaying: false,
                gotTheDetail: false,
                userMaxRetry: 1,
                retryCount: 0,
                isUserRetry: false,
                selectedMethod: localStorage.getItem("payMethodIndex") ? Number(localStorage.getItem("payMethodIndex")) : 0,
                selectedOtherMethod: {},
                showMethodList: false,
                startPollingTime: 0,
                oldStatus: '',
                gtagData: undefined,
                inApp: localStorage.getItem(AU_IN_APP) === 'true',
                loadSec: 0,
                percent: 0
            };
        },
        props: {
            dataInfo: {},
        },
        watch: {
            dataInfo: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.invoiceId = this.dataInfo.invoiceid;
                        this.checkInvoiceInfo(value);
                        const that = this;
                        this.$request.auPayInvoice(this.invoiceId, function (data) {
                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                that.payUrl = data.data.redirectUrl;
                            }
                        })
                    }
                }
            }
        },
        created() {
            this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
            //if (this.$route.query && this.$route.query.invoiceId) this.invoiceId = this.$route.query.invoiceId;
            //if (this.$route.params.redirect) this.payUrl = this.$route.params.redirect;
            if (this.$route.params.gtagData) this.gtagData = this.$route.params.gtagData;
            //if (this.$route.params.isRenew) this.isRenew = this.$route.params.isRenew;
            /*if (this.invoiceId !== 0) {
                if (this.payUrl.length === 0) {
                    const that = this;
                    this.$request.auPayInvoice(this.invoiceId, function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            that.payUrl = data.data.redirectUrl;
                        }
                    })
                }
                this.getInvoiceDetail();
            }*/
        },
        destroyed() {
            window.clearInterval(this.intervalId);
        },
        methods: {
            /*enterInvoiceDetail(){
                let params = {
                    invoiceId: this.invoiceId,
                };
                this.$router.push({
                    path: 'user/invoice',
                    name: 'invoiceDetail',
                    params: params,
                    query: this.$route.query
                });
            },*/
            startNow() {
                window.open(`${window.location.origin}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`, this.$inApp? '_self': '_blank');
            },
            payNow() {
                if (this.invoiceId != 0) {
                    const that = this;
                    if (this.isPaying) {// If is paying, that mean user trying repay
                        if (that.payUrl) {
                            window.open(that.payUrl, '_blank');
                        } else {
                            that.$request.auPayInvoice(that.invoiceId, function (data) {
                                if (data && (data.status === 200 || data.status === 201) && data.data) {
                                    that.payUrl = data.data.redirectUrl;
                                    window.open(that.payUrl, '_blank');
                                }
                            })
                        }
                        if (that.isUserRetry) {// If payFailed before, need reset all flag for repay
                            that.payStatus = that.$t('order/pay.Paragraph-1');
                            that.payFailed = false;
                            that.isUserRetry = false;
                            that.retryCount = 0;
                            that.startPolling();
                        }
                    } else if (this.paySuccess) {
                        window.open(`${window.location.origin}/my/service`, this.$inApp? '_self': '_blank')
                        // } else if (this.intervalId.length > 0) {
                    } else if (this.intervalId.length === 0) {
                        if (this.gtagData) {
                            let eventData = {
                                coupon: this.gtagData.coupon,
                                currency: 'USD',
                                value: this.gtagData.value,
                                traffic_type: this.gtagData.traffic_type,
                                items: [{
                                    item_id: this.gtagData.items.item_id,
                                    item_name: this.gtagData.items.item_name,
                                    affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                                    promotion_id: 0,
                                    promotion_name: 'none',
                                    item_variant: this.gtagData.items.item_variant,
                                    price: this.gtagData.value,
                                    currency: 'USD',
                                    coupon: this.gtagData.coupon
                                }]
                            }
                            if (this.inApp) {
                                // inApp event
                                if (window.InappWebInterface) {
                                    // Call Android interface
                                    window.InappWebInterface.webEvent('begin_checkout', JSON.stringify(eventData));
                                }
                            } else {
                                //send gtag
                                gtag('event', 'begin_checkout', eventData)
                            }
                        }
                        this.isPaying = true;
                        TweenMax.fromTo(document.getElementById('btn-container'),
                            0.3,
                            { marginTop: '0' },
                            {
                                css: { marginTop: that.isPc? '30px': '0.4rem' },
                                onStart: function () {
                                    TweenMax.fromTo(document.getElementById('submit-btn'),
                                        0.4,
                                        {
                                            width: that.isPc? '386px': '7.867rem',
                                            left: '0'
                                        },
                                        {
                                            width: that.isPc? '176px': '3.68rem',
                                            left: that.isPc? '210px': '4.187rem'
                                        }
                                    );
                                    TweenMax.fromTo(document.getElementById('submit-btn2'),
                                        0.4,
                                        { width: that.isPc ? '386px': '7.867rem' },
                                        {
                                            css: { width: that.isPc ? '176px': '3.68rem' },
                                            onComplete: function () {
                                                that.btnTips = that.$t('order/pay.Button-2');
                                                if (that.invoiceInfo.status !== i18n.t('Paid')) {
                                                    if (that.payUrl) {
                                                        window.open(that.payUrl, '_blank');
                                                    } else {
                                                        that.$request.auPayInvoice(that.invoiceId, function (data) {
                                                            if (data && (data.status === 200 || data.status === 201) && data.data) {
                                                                that.payUrl = data.data.redirectUrl;
                                                                window.open(that.payUrl, '_blank');
                                                            }
                                                        })
                                                    }
                                                }
                                            }
                                        }
                                    );
                                }
                            }
                        );
                        TweenMax.fromTo(document.getElementById('pay-sync'),
                            0.4,
                            { opacity: 0 },
                            { opacity: 1 }
                        );
                        this.startPolling();
                    } else {
                        this.$notification.open({
                            message: this.$t('order/pay.Fail-4'),
                            onClick: () => {
                            },
                        });
                    }
                } else {
                    this.$notification.open({
                        message: this.$t('order/pay.Fail-4'),
                        onClick: () => {
                        },
                    });
                }
            },
            userPaid() {
                if (this.payFailed) {
                    // If payFailed before, need to re-pay
                    this.$notification.open({
                        message: this.$t('order/pay.Fail-1'),
                        onClick: () => {
                        },
                    });
                } else {
                    this.payStatus = this.$t('order/pay.Status-3');
                    this.getInvoiceDetail();
                    this.isUserRetry = true
                }
            },
            getInvoiceDetail() {
                const that = this;
                this.$request.auGetInvoiceDetail(this.invoiceId, function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        that.checkInvoiceInfo(data.data)
                    } else {
                        that.$notification.open({
                            message: '獲取訂單詳情失敗，請稍後重試',
                            onClick: () => {
                            },
                        });
                    }
                });
            },
            checkInvoiceInfo(data) {
                this.invoiceInfo.invoiceid = data.invoiceid;
                this.invoiceInfo.balance = this.isLogin ? data.total : data.subtotal;
                this.invoiceInfo.duedate = data.duedate;
                this.invoiceInfo.date = data.date;
                this.invoiceInfo.status = i18n.t(data.status === 'Paid' ? 'order/pay.Status-2' : 'order/pay.Item-6');
                this.invoiceInfo = Object.assign({}, this.invoiceInfo, '');
                if (this.oldStatus === '') this.oldStatus = data.status;
                if (data.status === 'Paid') {
                    this.$emit('updateInvoiceInfo');
                    if (this.oldStatus === 'Unpaid') {
                        let exoclick = document.createElement("div");
                        exoclick.setAttribute('style', 'display: none; visibility: hidden');
                        exoclick.innerHTML = `<script  type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="be98d96266307dc8cbedc8001331a95a"><` + `/script>`;
                        document.body.appendChild(exoclick);

                        this.oldStatus = data.status;
                        this.pushPurchaseEvent(data);
                    }
                    window.clearInterval(this.intervalId);
                    this.userAccount = {
                        'address' : `${window.location.origin}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`,
                        'isFromPaymentPage' : true,
                    };
                    this.isPaying = false;
                    this.paySuccess = true;

                    const that = this;
                    let loadInterval = setInterval(function () {
                        if (that.loadSec !== 3) {
                            that.loadSec++;
                            that.percent += that.loadSec === 3 ? 34 : 33;
                        } else {
                            window.clearInterval(loadInterval);
                        }
                    },1000);

                    if (window.InappWebInterface) {
                        // Call Android interface
                        let params = {
                            'uuid': localStorage.getItem(AU_UUID),
                            'hmac': localStorage.getItem(AU_HMAC),
                        };
                        window.InappWebInterface.webEvent('account', JSON.stringify(params));
                    }
                } else if (this.isUserRetry) { // If this is user trigger to query
                    // Set the max retry (can not just try once)
                    if (this.retryCount > this.userMaxRetry) {
                        // Pay Failed, need to re-pay
                        window.clearInterval(this.intervalId);
                        this.intervalId = '';
                        this.payFailed = true;
                        this.payStatus = this.$t('order/pay.Fail-1');
                    }
                    this.retryCount ++
                }
            },
            pushPurchaseEvent(data) {
                // for google ads
                let items = [];
                items.push({
                    item_id: data.pid,
                    item_name: data.name,
                    promotion_id: 0,
                    promotion_name: '',
                    coupon:data.coupon,
                    affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                    item_variant: data.cycle,
                    price: this.isLogin ? data.total : data.subtotal,
                    currency: 'USD',
                });
                let eventData = {
                    affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                    coupon: data.coupon,
                    currency: 'USD',
                    items: items,
                    transaction_id: data.transid,
                    shipping: 0,
                    value: this.isLogin ? data.total : data.subtotal,
                    tax: 0,
                    traffic_type: process.env.NODE_ENV === 'development' ? 'internal' : 'normal',
                }
                if (this.inApp) {
                    // inApp event
                    if (window.InappWebInterface) {
                        // Call Android interface
                        window.InappWebInterface.webEvent('purchase', JSON.stringify(eventData));
                    }
                } else {
                    gtag('event', 'purchase', eventData)
                }

                // for facebook ads
                fbq('track', 'Purchase', {currency: "USD", value: this.isLogin ? data.total : data.subtotal});

                // for twitter ads
                twq('track','Purchase', {
                    //required parameters
                    value: this.isLogin ? data.total : data.subtotal,
                    currency: 'USD',
                    num_items: '1',
                });

                // for dable
                dablena('track', 'Purchase', {value: this.isLogin ? data.total : data.subtotal, currency: 'USD'});
            },
            startPolling() {
                const that = this;
                if (that.intervalId.length === 0) {
                    that.startPollingTime = new Date().getTime();
                    that.intervalId = setInterval(function () {
                        if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
                            window.clearInterval(that.intervalId);
                            that.startPollingTime = new Date().getTime();
                            that.intervalId = setInterval(function () {
                                if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 10) {
                                    window.clearInterval(that.intervalId);
                                    that.intervalId = '';
                                } else {
                                    that.getInvoiceDetail();
                                }
                            },8000);
                        } else {
                            that.getInvoiceDetail();
                        }
                    }, 4000);
                }
            },
            openZendesk() {
                window.zE('webWidget', 'toggle');
            },
            closeDialog(){
                this.$emit('closeDialog')
            }
        }
    };
</script>

