                                }
                                &.unionpay {
                                    width: 28px;
                                    background: url('../assets/pay/union-pay.svg') center no-repeat;
                                }
                                &.master {
                                    width: 28px;
                                    background: url('../assets/pay/master.svg') center no-repeat;
