<script>
    import {AU_AFF, AU_EMAIL} from "@/requestServer";
    import PaymentDialog from "@/dialog/PaymentDialog";
    export default {
        name: "InvoiceDetail",
        components: {
            PaymentDialog
        },
        data () {
            return {
                isPc : this.$isPc(),
                printObj: {
                    id: "printMe",
                    popTitle: 'auVPN',
                    extraCss: '',
                    extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>'
                },
                invoiceItem: {},
                loading: true,
                intervalId: '',
                iconLoading: false,
                payPerson: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? 'Anonymous' : localStorage.getItem(AU_EMAIL) : 'Anonymous',
                oldStatus: '',
                showPaymentDialog: false,
            }
        },
        mounted() {
            if (this.$route.params.invoiceId != 0) this.getInvoiceDetail();
        },
        destroyed() {
            window.clearInterval(this.intervalId);
        },
        methods: {
            updateInvoiceInfo() {
                this.getInvoiceDetail();
            },
            closeDialog(){
                this.showPaymentDialog = false;
            },
            printRow(){
                this.$print(this.$refs.print) // 使用
            },
            startPolling() {
                const that = this;
                if (that.intervalId.length === 0) {
                    that.startPollingTime = new Date().getTime();
                    that.intervalId = setInterval(function () {
                        if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
                            window.clearInterval(that.intervalId);
                        } else {
                            that.getInvoiceDetail();
                        }
                    }, 4000);
                }
            },
            getInvoiceDetail() {
                const that = this;
                this.$request.auGetInvoiceDetail(this.$route.params.invoiceId, function (data) {
                    that.loading = false;
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        that.invoiceItem = data.data;
                        if (that.oldStatus.length === 0) that.oldStatus = data.data.status;
                        if (data.data.status === 'Paid') {
                            that.iconLoading = false;
                            if (that.oldStatus === 'Unpaid') {
                                that.oldStatus = data.data.status;
                                that.pushPurchaseEvent(data);
                            }
                            window.clearInterval(that.intervalId);
                        }
                    }
                });
            },
            pushPurchaseEvent(data) {
                // for google ads
                let items = [];
                items.push({
                    item_id: data.data.pid,
                    item_name: data.data.name,
                    promotion_id: 0,
                    promotion_name: '',
                    coupon: data.data.coupon,
                    affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                    item_variant: data.data.cycle,
                    price: data.data.total,
                    currency: 'USD',
                });
                gtag('event', 'purchase', {
                    affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                    coupon: data.data.coupon,
                    currency: 'USD',
                    items: items,
                    transaction_id: data.data.transid,
                    shipping: 0,
                    value: data.data.total,
                    tax: 0,
                    traffic_type: process.env.NODE_ENV === 'development' ? 'internal' : 'normal',
                })

                // for facebook ads
                fbq('track', 'Purchase', {currency: "USD", value: data.data.total});

                // for twitter ads
                twq('track','Purchase', {
                    //required parameters
                    value: data.data.total,
                    currency: 'USD',
                    num_items: '1',
                });

                // for dable
                dablena('track', 'Purchase', {value: data.data.total, currency: 'USD'});

                // event fire
                this.$eventFire.purchase()
                if (data.data.total >= 1) {
                    this.$eventFire.subscribe()
                } else {
                    this.$eventFire.startTrial()
                }
            },
            payInvoice(id) {
                this.showPaymentDialog = true
                /*this.$router.push({
                    path: 'order/pay',
                    name: 'payment',
                    params: {
                        isRenew: true,
                    },
                    query: {
                        ...this.$route.query,
                        invoiceId: id,
                    }
                });*/
                /*this.iconLoading = true;
                const that = this;
                this.$request.auPayInvoice(id,function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data){
                        that.startPolling();
                        window.open(data.data.redirectUrl, '_blank');
                    } else {
                        that.iconLoading = false;
                    }
                });*/
            },
            countUnpaidAmount(){
                let total = this.countBillTotal();
                this.invoiceItem.transactions.transaction.forEach(item => {
                    if (this.invoiceItem.status === 'Refunded' && item.refundid !== 0) {
                        total = Number(total - Number(item.amountout));
                    } else if (this.invoiceItem.status === 'Paid' && item.refundid === 0){
                        total = Number( total - Number(item.amountin));
                    }
                });
                return Number(total).toFixed(2);
            },
            countBillTotal() {
                let total = 0;
                this.invoiceItem.items.item.forEach(item => {
                    total = Number(item.amount) + total;
                });
                return Number(total).toFixed(2);
            },
            openZw() {
                window.zE('webWidget', 'open');
                setTimeout(function () {
                    // Open the channel
                    document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
                }, 1500);
            },
        }
    }
</script>

